.products_item {
    position: relative;
    -webkit-box-flex: 1;
        -ms-flex: 1 1 350px;
            flex: 1 1 350px;
    height: 350px;
    margin: 2% 2% 0 0;
    overflow: hidden;
    padding: 1.5em;
  /*!*border: 1px solid rgba(0, 0, 0, 0.09);*!*/
    box-sizing: border-box;
    -webkit-transition: .2s ease;
    transition: .2s ease;
}
.products_list::before,.products_list::after {
    display: block;
}
.products_list::after {
    content: "";
    display: block;
    clear: both;
}
.products_list__search {
    padding: 1em 0;
    width: 50%;
}
@media screen and (max-width: 1030px) {
.products_list__search {
      width: 100% !important;
}
}
.products_list__search input {
    width: 100%;
}
.products_list__search input {
    padding: .8em 1em;
    font-size: 1rem;
    box-sizing: border-box;
}
