      object-fit: contain;
}
.wrap_title {
  display: block;
}
.wrap_title span {
  display: block;
  line-height: 2em;
  text-align: left;
}
.products_item__name {
  font-size: 1rem;
}
.products_item__cost {
  font-size: 1rem;
}
.products_item__available {
  font-size: .7rem;
}
.product_text {
  text-align: left;
  font-size: .9rem;
}
.isAdd {
  /*background: rgba(244, 244, 244, 0.52);*/
  box-shadow: inset 0px -7px 0px 0px rgba(160, 160, 160, 0.29) !important;
}
.logo {
  float: left;
  width: 50px;
  margin-left: 1em;
}
.logo img {
  width: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}
.header {
     display: block;
     margin: 0 auto;
     width: 80%;
     top: 0;
     font-size: 1.1rem;
     background: #fff;
     color: rgba(0,0,0,.44);
     box-shadow: 0px 0px 3px 1px rgba(0,0,0,.15);
     letter-spacing: 1px;
     font-weight: 100;
     box-sizing: border-box;
    z-index: 10;
}
@media screen and (max-width: 600px) {
.header {
      width: 100% !important;
}
}
.navigation {
     margin: 0;
}
.navigation li {
  display: inline-block;
}
.navigation li a {
  color: rgba(173, 173, 173, 0.98);
    -webkit-transition: .2s ease;
    transition: .2s ease;
}
.navigation li a:hover {
  color: rgba(50, 50, 50, 0.78);
}
li {
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
}
.active {
    box-shadow: inset 0 -3px 0px 0px rgba(0, 0, 0, 0.31);
}
.show {
    display: block !important;
}
.add-to-cart {
  position: absolute;
  right: 1em;
  bottom: 1em;
  width: 50px;
  background: none;
  border: none;
  cursor: pointer;
  z-index: 2;

  -webkit-transition: .2s ease;

  transition: .2s ease;
}
.add-to-cart:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.add-to-cart:focus {
    outline: none;
}
.add-to-cart--hidden {
  display: none;
}
.add-to-cart__img >img {
    width: 100%;
}
.add-to-cart__add-more {
  position: absolute;
  top: 0;
  padding: .5em;
  letter-spacing: 1px;
  -webkit-transform: translate(-50%,0%);
          transform: translate(-50%,0%);
  color: #000000;
  -webkit-transition: .2s ease;
  transition: .2s ease;
  color: #ffffff;
  background: rgba(244, 94, 37, 0.25);
  opacity: 0;
  z-index: 1;
}
.add-to-cart__add-more.add-to-cart__add-more--move {
  -webkit-animation: move .3s ease;
          animation: move .3s ease;
}
@-webkit-keyframes move {
0% {
    -webkit-transform: translate(-50%,0%);
            transform: translate(-50%,0%);
}
50% {
    opacity: 1;
}
100% {
    -webkit-transform: translate(-50%,-150%);
            transform: translate(-50%,-150%);
    opacity: 0;
}
}
@keyframes move {
0% {
    -webkit-transform: translate(-50%,0%);
            transform: translate(-50%,0%);
}
50% {
    opacity: 1;
}
100% {
    -webkit-transform: translate(-50%,-150%);
            transform: translate(-50%,-150%);
    opacity: 0;
}
}
.add-to-cart__added {
  position: absolute;
  bottom: 0;
  right: 50%;

  -webkit-transform: translate(50%,100%);

          transform: translate(50%,100%);
  -webkit-transition: .2s ease;
  transition: .2s ease;
  color: #ffffff;
  background: rgba(244, 94, 37, 0.65);
}
.add-to-cart__added a {
    display: inline-block;
    color: #ffffff;
    padding: .5em .8em;
    letter-spacing: 1px;
}
.add-to-cart__added.isAdded {
    -webkit-transform: translate(50%,-50%);
            transform: translate(50%,-50%);
}
.add-to-cart__added.isAdded:hover {
    background: rgba(244, 94, 37, 0.82);
    /*text-decoration: underline;*/
}
.add-to-cart__added--show {
  display: block;
}
.list-complete-item {
    -webkit-transition: all .2s;
    transition: all .2s;
    margin-top: 10px;
}
.list-complete-enter, .list-complete-leave-to
    /* .list-complete-leave-active для <2.1.8 */ {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
}
.list-complete-leave-active {
    position: absolute;
}





